(function() {
  'use strict';

  angular
    .module('demoApp', ['ngAnimate', 'ngAria', 'ui.router', 'ngMaterial', 'ui.ngTextRoll', 'hljs']);

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('NumberDemoController', NumberDemoController);

  /** @ngInject */
  function NumberDemoController(demoUtilSvc) {
    var vm = this;

    vm.theValue = demoUtilSvc.getRandomDecimal(100.00, 900.00);
    vm.changeValue = function() {
      vm.theValue = demoUtilSvc.getRandomDecimal(100.00, 900.00);
    }

    vm.theOtherValue = 12345.67;

  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('IntroController', IntroController);

  /** @ngInject */
  function IntroController($scope, $timeout, demoUtilSvc) {
    var vm = this;
    vm.lower = 10000000;
    vm.upper = 50000000;
    vm.time = 1000;
    vm.theValue = demoUtilSvc.getRandomInt(vm.lower, vm.upper);

    var tm;
    var onTimer = function() {
      vm.theValue = demoUtilSvc.getRandomInt(vm.lower, vm.upper);
      vm.time = demoUtilSvc.getRandomInt(2000, 3000);
      tm = $timeout(onTimer, vm.time);
    };
    tm = $timeout(onTimer, vm.time);

    $scope.$on('$destroy', function() {
      if (tm) {
        $timeout.cancel(tm);
      }
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .factory('demoUtilSvc', function() {
      var svc = {};

      svc.getRandomInt = function(min, max) {
        return parseInt((Math.random() * (max - min) + min));
      };

      svc.getRandomDecimal = function(min, max) {
        return parseFloat((Math.random() * (max - min) + min).toFixed(2));
      };

      svc.getRandomDate = function() {
        var inc = parseInt(svc.getRandomDecimal(1, 99));
        var date = new Date();
        date.setDate(date.getDate() + inc);
        return date;
      };

      return svc;
    });

})();

(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($mdSidenav, $timeout, $rootScope, $scope, $state) {
    var vm = this;

    vm.demoMenuItems = [{
        label: 'Introduction',
        stateName: 'main.intro'
      }, {
        label: 'Number',
        stateName: 'main.number'
      }
      /*{ label: 'Currency', stateName: 'currency' },
            { label: 'Date', stateName: 'date' }*/
    ];
    vm.demoMenuInx = 0;

    var getLabel = function() {
      angular.forEach(vm.demoMenuItems, function(item, inx) {
        if (item.stateName === $state.current.name) {
          vm.currentLabel = item.label;
          vm.demoMenuInx = inx;
          return true;
        }
      });
    };
    getLabel();

    vm.openNav = function() {
      $timeout(function() {
        $mdSidenav('left').open();
      });
    };

    var locationChangeSuccessEvent = $rootScope.$on('$locationChangeSuccess', function() {
      getLabel();
      $timeout(function() {
        $mdSidenav('left').close();
      });
    });

    $scope.$on('$destroy', function() {
      if (locationChangeSuccessEvent) {
        locationChangeSuccessEvent();
      }
    });

  }

})();

